import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useHistory, useLocation } from "react-router-dom";
import { toast } from "react-toastify";
import {
  FaCode,
  FaColumns,
  FaExchangeAlt,
  FaFileAlt,
  FaPlus,
  FaRemoveFormat,
  FaSync,
  FaTrash,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import queryString from "query-string";
import { Formik } from "formik";
import * as Yup from "yup";

import "./SubjectGoalEdit.css";

import { RoutesString } from "pages/routesString";
import subjectGoalAPI from "api/common/subject-goal";
import syllabusAPI from "api/common/syllabus";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import PageHeader from "./components/PageHeader/PageHeader";
import CDIOOutputStandardAPI from "api/common/education-major-output-standard";
import Select from "react-select";
import i18n from "i18n/i18n";

export default function SubjectGoalEdit() {
  const { t } = useTranslation();

  const history = useHistory();

  const location = useLocation();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [initialValues, setInitialValues] = useState({
    ma: "",
    moTa: "",
    syllabus: "",
    chuanDauRaCDIO: []
  });
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [syllabuses, setSyllabuses] = useState([]);
  const [CDIOOutputStandards, setCDIOOutputStandards] = useState([]);

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    }
    try {
      const syllabusesRes = await syllabusAPI.getAll();
      setSyllabuses(syllabusesRes.contents);
      if (!parsed.id) {
        setInitialValues({
          ...initialValues,
          syllabus: syllabusesRes.contents[0].id,
        });
      }
      const CDIOOutputStandardsRes = await CDIOOutputStandardAPI.getAll();
      const CDIOOutputStandardsOptions = CDIOOutputStandardsRes.contents.map(
        (item) => {
          return { value: item.id, label: item.ma };
        }
      );
      setCDIOOutputStandards(CDIOOutputStandardsOptions);
      // eslint-disable-next-line
    } catch (err) {
    } finally {
      setIsLoading(false);
    }
  }, [location]);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await subjectGoalAPI.getById(id);
      setInitialValues({
        ma: res.ma,
        moTa: res.moTa,
        syllabus: res.syllabus.id,
        chuanDauRaCDIO: res.chuanDauRaCDIO.map((item) => ({ value: item.id, label: item.ma }))
      });
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditOnClick = async function (values, isValid) {
    if (!isValid) {
      return;
    }
    if (isUpdatePage) {
      const body = {
        ...values,
        syllabus: parseInt(values.syllabus),
        chuanDauRaCDIO: values.chuanDauRaCDIO.map(item => item.value.toString())
      };
      try {
        const res = await subjectGoalAPI.putById(id, body);
        if (res.status === 200) {
          setNotification({
            color: "success",
            isOpen: true,
            messages: [t("admin.UPDATE_SUCCESS")],
          });
          updateForm(id);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    } else {
      const body = {
        ...values,
        syllabus: parseInt(values.syllabus),
        chuanDauRaCDIO: values.chuanDauRaCDIO.map(item => item.value.toString())
      };
      try {
        const res = await subjectGoalAPI.post(body);
        if (res.id) {
          setNotification({
            color: "danger",
            isOpen: false,
            messages: [],
          });
          history.push(`${RoutesString.ADMIN_SUBJECT_GOAL_EDIT}?id=${res.id}`);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    }
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    try {
      const res = await subjectGoalAPI.deleteById(id);
      if (res.status === 200) {
        toast.success(i18n.t("admin.common.DELETED"));
        history.push(
          `${RoutesString.ADMIN_SUBJECT_GOAL_LIST}`
        );
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.subject_goal_edit.UPDATE_SUBJECT_GOAL")
            : t("admin.subject_goal_edit.ADD_SUBJECT_GOAL")
        }
        items={[
          {
            href: RoutesString.ADMIN_SUBJECT_GOAL_LIST,
            title: t("admin.subject_goal_list.SUBJECT_GOAL_LIST"),
          },
        ]}
      />

      <PageHeader
        titleHeader={
          isUpdatePage
            ? t("admin.subject_goal_edit.UPDATE_SUBJECT_GOAL")
            : t("admin.subject_goal_edit.ADD_SUBJECT_GOAL")
        }
      />

      <div className="page-content">
        <Formik
          initialValues={initialValues}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({
            ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
            moTa: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
          })}
        >
          {({ values, errors, dirty, isValid, handleChange, handleReset, setFieldValue }) => (
            <Form>
              <Notification
                color={notification.color}
                isOpen={notification.isOpen}
                messages={notification.messages}
              />
              <FormGroup className="row">
                <Label for="ma" className="col-lg-3">
                  {t("admin.subject_goal_edit.CODE")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaCode />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="ma"
                      placeholder={t(
                        "admin.subject_goal_edit.CODE_PLACEHOLDER"
                      )}
                      value={values.ma}
                      onChange={handleChange}
                      invalid={errors.ma}
                      readOnly={isUpdatePage}
                    />
                  </InputGroup>
                  {errors.ma && <FormText color="danger">{errors.ma}</FormText>}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="moTa" className="col-lg-3">
                  {t("admin.subject_goal_edit.DESCRIPTION")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaFileAlt />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="textarea"
                      name="moTa"
                      placeholder={t(
                        "admin.subject_goal_edit.DESCRIPTION_PLACEHOLDER"
                      )}
                      value={values.moTa}
                      onChange={handleChange}
                      invalid={errors.moTa}
                    />
                  </InputGroup>
                  {errors.moTa && (
                    <FormText color="danger">{errors.moTa}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="syllabus" className="col-lg-3">
                  {t("Syllabus")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaColumns />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="syllabus"
                      value={values.syllabus}
                      onChange={handleChange}
                    >
                      {syllabuses &&
                        syllabuses.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {`${item.monHoc.ma} - ${item.heDaoTao.ten} - ${item.namHoc.ten}`}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="chuanDauRaCDIO" className="col-lg-3">
                  {t("admin.subject_goal_edit.CDIO_OUTPUT_STANDARD")}
                </Label>
                <div className="col-lg-6">
                  <Select
                    value={values.chuanDauRaCDIO}
                    onChange={(e) => setFieldValue("chuanDauRaCDIO", Array.isArray(e) ? e : [])}
                    isMulti
                    closeMenuOnSelect={false}
                    options={CDIOOutputStandards}
                    placeholder={t(
                      "admin.subject_goal_edit.CDIO_OUTPUT_STANDARD_PLACEHOLDER"
                    )}
                  />
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup>
                <Button
                  color="success"
                  disabled={isUpdatePage ? !isValid : !(isValid && dirty)}
                  onClick={() =>
                    handleEditOnClick(
                      values,
                      isUpdatePage ? isValid : isValid && dirty
                    )
                  }
                >
                  {isUpdatePage ? (
                    <>
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </>
                  ) : (
                    <>
                      <FaPlus /> {t("admin.common.ADD")}
                    </>
                  )}
                </Button>
                &emsp;
                {isUpdatePage && (
                  <>
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleDeleteOnClick}>
                      <FaTrash /> {t("admin.common.DELETE")}
                    </Button>
                  </>
                )}
              </FormGroup>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
